 * {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
 }

 html {
   scroll-behavior: smooth;
   font-family: $font-first;
   font-weight: 600;
   font-style: normal;
   font-size: 14px;

   &:has(.menu:target) {
     overflow: hidden;
   }
 }

 .container {
   margin-inline: auto;
   max-width: 1148px;
   padding-inline: 20px;

   @include on-tablet {
    padding-inline: 40px;
  }
}

.subtitle {
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 3px;
  text-transform: uppercase;
}

.title-section {
  color: $color-text-title;
  font-size: 32px;
  line-height: 150%;
  text-align: center;

  &-left {
    text-align: left;
  }
}

.logo {
  width: fit-content;
  & > img{
    width: 46px;
    height: 28px;
    transition: all $time-animation;

    &:hover {
      transform: scale(1.1);
    }
  }
}
